import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import foto1 from "../../images/two-yellow-labrador-retriever-puppies-1108099.jpg"

const Ubuntu = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Las vitaminas que necesita tu perro"
                meta="Las tiendas de comestibles y las farmacias están llenas de fila tras fila de vitaminas humanas. Con tantas opciones para nosotros, es completamente lógico que nos preguntemos si nuestras mascotas podrían beneficiarse de algunas de estas vitaminas también."
                description="Las tiendas de comestibles y las farmacias están llenas de fila tras fila de vitaminas humanas. Con tantas opciones para nosotros, es completamente lógico que nos preguntemos si nuestras mascotas podrían beneficiarse de algunas de estas vitaminas también."
                lang="es"
            />
            <h1>Vitaminas que tu perro necesita para un estilo de vida saludable</h1>
            <hr/>
            <p class="introductionText">Las tiendas de comestibles y las farmacias est&aacute;n llenas de fila tras fila de vitaminas
                humanas. Con tantas opciones para nosotros, es completamente l&oacute;gico que nos preguntemos si
                nuestras mascotas podr&iacute;an beneficiarse de algunas de estas vitaminas tambi&eacute;n.</p>
            <hr/>
            <p></p>
            <div>
                <p></p>
                <p>Pero, &iquest;los perros realmente necesitan vitaminas? &iquest;Hay alg&uacute;n riesgo asociado con
                    las vitaminas para perros? &iquest;Qu&eacute; vitaminas deber&iacute;a darle a su perro? He
                    aqu&iacute; algunas respuestas.</p>
                <p></p>
                <h2>&iquest;Qu&eacute; son las vitaminas?</h2>
                <p><a className="link" href="/">Las vitaminas</a> son compuestos org&aacute;nicos necesarios para mantener la vida. La mayor&iacute;a se
                    encuentran de forma natural en los alimentos. El cuerpo de los animales necesita vitaminas para el
                    crecimiento y el mantenimiento.</p>
                <p></p>

                <div style={{textAlign:`center`,margin:'50px'}}>
                    <img src={foto1} style={{width:`50vh`}} alt="dos cachorros"></img>
                </div>
                <p>Probablemente est&eacute;s familiarizado con la mayor&iacute;a de las vitaminas que el cuerpo humano
                    y el animal necesitan:</p>
                <p></p>

                <ul style={{fontSize: '20px', color: '#01adb9',marginTop:'40px'}}>
                    <li><a className="link" style={{textDecoration: 'underline'}} href="/vitaminaA"><b>vitamina
                        A</b></a>
                    </li>
                    <li><a className="link" style={{textDecoration: 'underline'}} href="/vitaminaB"><b>vitamina
                        B</b></a>
                    </li>
                    <li><a className="link" style={{textDecoration: 'underline'}} href="/vitaminaC"><b>vitamina
                        C</b></a>
                    </li>
                    <li><a className="link" style={{textDecoration: 'underline'}} href="/vitaminaD"><b>vitamina
                        D</b></a>
                    </li>
                    <li><a className="link" style={{textDecoration: 'underline'}} href="/vitaminaE"><b>vitamina
                        E</b></a>
                    </li>
                    <li><a className="link" style={{textDecoration: 'underline'}} href="/vitaminaK"><b>vitamina
                        K</b></a>
                    </li>
                </ul>
                <p><b>Los perros tambi&eacute;n necesitan estas vitaminas, aunque es muy importante que nos demos cuenta de
                    que pueden necesitarlas en cantidades diferentes a las que nosotros necesitamos antes de tomar los
                    suplementos.</b></p>
                <p></p>
                <h2>Vitamina A para perros</h2>
                <p>La vitamina A, en caso de que te lo hayas preguntado, es la vitamina de las zanahorias que es
                    responsable de esa buena visi&oacute;n que tus padres te prometieron. Esta vitamina soluble en grasa
                    tambi&eacute;n es responsable del crecimiento, el desarrollo fetal, la funci&oacute;n
                    inmunol&oacute;gica y la funci&oacute;n celular.</p>
                <p></p>
                <h2>Vitaminas B para perros</h2>
                <p>Las vitaminas B son un grupo de vitaminas importantes que juegan un papel en la salud de su
                    perro.</p>
                <p></p>
                <p>La tiamina ayuda a regular el metabolismo de la energ&iacute;a y los carbohidratos, y activa los
                    canales de iones en el tejido neural.</p>
                <p>La riboflavina, la B12 y la niacina ayudan a facilitar la funci&oacute;n de las enzimas.</p>
                <p><b>La vitamina B6 es especialmente vital. Esta vitamina es responsable de la generaci&oacute;n de
                    glucosa, la funci&oacute;n de los gl&oacute;bulos rojos y del sistema nervioso, la regulaci&oacute;n
                    hormonal, la respuesta inmunol&oacute;gica, la s&iacute;ntesis de niacina y la activaci&oacute;n de
                    los genes.</b></p>
                <p>El &aacute;cido pantot&eacute;nico ayuda con el metabolismo de la energ&iacute;a.</p>
                <p>El &aacute;cido f&oacute;lico juega un papel en el metabolismo de los amino&aacute;cidos y los
                    nucle&oacute;tidos y en la s&iacute;ntesis de las prote&iacute;nas mitocondriales.</p>
                <h2>Vitamina C para perros</h2>
                <p>La vitamina C es un antioxidante importante. Busca los radicales libres potencialmente da&ntilde;inos
                    en el cuerpo y puede ayudar a reducir la inflamaci&oacute;n y el envejecimiento cognitivo. Los
                    perros pueden sintetizar la vitamina C por s&iacute; mismos en sus h&iacute;gados, pero en algunos
                    casos la suplementaci&oacute;n puede ofrecer beneficios para la salud.</p>
                <p></p>
                <h2>Vitamina D para perros</h2>
                <p>La vitamina D, o la &quot;vitamina del sol&quot;, permite que el cuerpo de su perro equilibre los
                    minerales como el f&oacute;sforo y el calcio para un crecimiento &oacute;seo saludable. Sin ella, su
                    perro no ser&iacute;a capaz de desarrollarse adecuadamente o de mantener los m&uacute;sculos y
                    huesos sanos.</p>
                <p></p>
                <h2>Vitamina E para perros</h2>
                <p>La vitamina E es una de las defensas de su perro contra el da&ntilde;o oxidativo. Esta vitamina
                    soluble en grasa tambi&eacute;n es esencial para la funci&oacute;n celular y el metabolismo de la
                    grasa. Las deficiencias pueden conducir a la degeneraci&oacute;n de los ojos y los m&uacute;sculos y
                    a problemas reproductivos.</p>
                <p></p>
                <h2>Vitamina K para perros</h2>
                <p>La vitamina K es una vitamina soluble en grasa que activa la capacidad de coagulaci&oacute;n de la
                    sangre de su perro. La ingesti&oacute;n de ciertos venenos para ratas y ratones inhibe la capacidad
                    de los perros para utilizar la vitamina K en sus cuerpos, lo que conduce a hemorragias y a la muerte
                    si no se trata.</p>
                <p></p>
                <p></p>
                <h2>&iquest;Necesitan los perros suplementos vitam&iacute;nicos?</h2>
                <p>Su perro obtiene sus vitaminas de la comida para perros. Las dietas comerciales de alimentos para
                    perros etiquetadas como &quot;completas y equilibradas&quot; est&aacute;n especialmente formuladas
                    para contener todas las vitaminas, minerales y nutrientes que su perro necesita.</p>
                <p></p>
                <p><b>Los alimentos destinados a las diferentes etapas de la vida, como la comida para cachorros, la comida
                    para perros adultos y la comida para perros mayores, contienen diferentes niveles de ciertas
                    vitaminas, dependiendo de los requisitos de esa etapa de la vida. </b>Esto es especialmente importante
                    en el caso de los alimentos para cachorros de razas grandes, ya que estas razas pueden desarrollar
                    enfermedades, como la displasia de cadera, si su alimento contiene vitaminas y minerales, como el
                    calcio, que los hacen crecer demasiado r&aacute;pido.</p>
                <p></p>
                <p>Los perros alimentados con una dieta comercial apropiada no deber&iacute;an requerir suplementos
                    vitam&iacute;nicos a menos que un veterinario les recomiende lo contrario. Los perros alimentados
                    con una dieta casera, por otra parte, pueden requerir suplementos para asegurarse de que
                    est&aacute;n recibiendo suficientes vitaminas con sus comidas. Sin embargo, estas vitaminas deben
                    administrarse de acuerdo con la dieta, seg&uacute;n la nutricionista veterinaria Susan Wynn, DVM. No
                    basta con alimentar a estos perros con una vitamina durante la cena.</p>
                <p></p>
                <h2>&iquest;Existen riesgos asociados a las vitaminas para perros?</h2>
                <p>Las vitaminas son absolutamente vitales para la vida. No deber&iacute;a sorprendernos que algo tan
                    esencial pueda ser potencialmente peligroso en grandes cantidades.</p>
                <p></p>
                <p>Ya sabe que demasiado calcio mineral puede causar problemas &oacute;seos en los cachorros de razas
                    grandes. Puede que no sepa que las vitaminas tambi&eacute;n pueden causar problemas.</p>
                <p></p>
                <p><b>Demasiada vitamina A puede causar deshidrataci&oacute;n, dolor en las articulaciones e incluso puede
                    da&ntilde;ar los vasos sangu&iacute;neos de su perro. Adem&aacute;s, hay muy pocos estudios que
                    hayan probado la seguridad a largo plazo de los suplementos vitam&iacute;nicos para perros, y
                    algunos suplementos contienen otros ingredientes, como hierbas, que pueden interactuar con ciertos
                    medicamentos.</b></p>
                <p></p>
                <p>Puede evitar estos riesgos elaborando un plan nutricional con su veterinario.</p>
                <h2>C&oacute;mo elegir una vitamina para perros</h2>
                <p>La mejor manera de elegir una vitamina para perros es hablar con su veterinario sobre
                    qu&eacute; suplementos vitam&iacute;nicos para perros necesita su perro, si es que los necesita.
                    Seg&uacute;n el profesor Tony Buffington, DVM, PhD, &quot;<b>la mayor&iacute;a de la gente lo hace
                        porque quiere, no porque sea necesario&quot;, cuando se trata de dar vitaminas a los perros.</b></p>
                <p></p>
                <p>Hay excepciones a esta regla. Si su perro necesita vitaminas, ya sea para complementar su dieta
                    casera o debido a una condici&oacute;n m&eacute;dica o deficiencia, entonces debe asegurarse de que
                    su perro reciba el suplemento vitam&iacute;nico apropiado.</p>
                <p></p>
                <p>Hable con su veterinario sobre la dosis de vitaminas apropiada para su perro. Las vitaminas humanas a
                    menudo tienen concentraciones diferentes de vitaminas que las vitaminas espec&iacute;ficamente
                    hechas para los perros, e incluso pueden contener aditivos que son perjudiciales para los perros.
                    Esto significa que debe seguir con un suplemento vitam&iacute;nico veterinario o uno hecho
                    espec&iacute;ficamente para perros, y siempre debe comprobar la etiqueta para asegurarse de que la
                    vitamina contiene la cantidad apropiada de las vitaminas que su perro necesita.</p>
                <p></p>
                <p>Seg&uacute;n el Dr. Wynn, el veterinario puede incluso recomendar que se complemente la dieta de su
                    perro con frutas y verduras espec&iacute;ficas, en lugar de suplementos vitam&iacute;nicos.</p>
                <p></p>
                <p>Las vitaminas son algunos de los componentes b&aacute;sicos de la salud de un perro. Alimentar a su
                    perro con un alimento de buena calidad es la mejor manera de asegurar que su perro obtenga todas las
                    vitaminas que necesita, pero si tiene alguna pregunta sobre los suplementos vitam&iacute;nicos,
                    aseg&uacute;rese de hablar con su veterinario.</p>
                <p></p>
                <p>AKC participa en programas de publicidad de afiliados dise&ntilde;ados para proporcionar un medio
                    para que los sitios web ganen tarifas de publicidad al anunciar y enlazar con akc.org. Si compras un
                    producto a trav&eacute;s de este art&iacute;culo, podemos recibir una parte de la venta.</p>
                <p></p>
            </div>

        </Layout>
    )
}

export default Ubuntu
